@import url(https://fonts.googleapis.com/css2?family=Roboto+Flex);
:root {
    --white: #f2f2f2;
    --font: "Roboto Flex";
    --gray: #707070;
    --blue: #00b4ba;
    --darkgray: #3d3d3d;
  }
  
  * {
    font-family: "Roboto Flex";
    font-family: var(--font);
  }
  
  .hero {
    margin: 0;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    z-index: 1;
  }
  
  h1 {
    font-size: 200%;
    -webkit-text-decoration: underline solid (--white);
            text-decoration: underline solid (--white);
    transition: 0.5s;
    margin-top: 60px;
    margin-bottom: 60px;
    text-align: left;
    margin-left: 30px;
  }
  
  h1:hover {
    color: var(--red);
    text-decoration: none;
    text-shadow: 4px 4px 4px #707070;
    text-shadow: 4px 4px 4px var(--gray);
  }
  
  .about-me p {
    font-size: 150%;
    text-align: center;
    margin-top: 50px;
    margin-left: 300px;
    margin-right: 100px;
    margin-bottom: 50px;
    transition: 0.5s;
  }
  
  .about-me a,
  .repolink {
    color: black;
    text-decoration: none;
    transition: 1s;
  }
  
  .about-me p:hover a,
  .repolink:hover {
    color: #00b4ba;
    color: var(--blue);
  }
  
  .about-me p:hover {
    text-shadow: 3px 3px 3px black;
    color: #f2f2f2;
    color: var(--white);
  }
  
  .custom-container,
  .custom-container a,
  .custom-container img {
    width: 450px;
    margin-top: 10px;
    position: relative;
    display: inline-block;
    transition: 0.5s;
    margin-right: 0.5rem;
    margin-left: 0.5rem;
  }
  
  .repolink {
    text-decoration: none;
    font-size: 140%;
    margin-left: 5rem;
  }
  
  .caption {
    position: absolute;
    top: 50px;
    left: 5px;
    background-color: black;
    border-radius: 8px;
    padding: 25px;
    margin-left: 15px;
  }
  
  .work div {
    color: #f2f2f2;
    color: var(--white);
    transition: 1s;
  }
  
  .work img:hover {
    box-shadow: 4px 4px 4px #707070;
    box-shadow: 4px 4px 4px var(--gray);
    border: 6px solid #f2f2f2;
    border: 6px solid var(--white);
  }
  
  .work a:hover div {
    background-color: #f2f2f2;
    background-color: var(--white);
    color: var(--red);
  }
  
  .work img {
    border: 2px solid #707070;
    border: 2px solid var(--gray);
  }
  
  .contact-me a {
    color: black;
    text-decoration: none;
    transition: 0.5s;
  }
  
  .contact-me ul li {
    font-size: 200%;
    margin-left: 100px;
    flex-wrap: wrap;
    margin-bottom: 50px;
  }
  
  @media screen and (max-width: 992px) {
    header {
      padding-bottom: 50px;
    }
  
    header h1 {
      font-size: 170%;
    }
  
    .about-me p {
      font-size: 170%;
      margin-left: 100px;
    }
  
    .work a,
    .work figure {
      margin-left: 90px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  
    .hero {
      height: 850px;
    }
  }
  
  @media screen and (max-width: 768px) {
    header {
      padding-bottom: 60px;
    }
  
    .work a,
    .work figure {
      margin-left: 35px;
    }
  
    .hero {
      height: 900px;
    }
  
    header a,
    .contact-me a {
      color: black;
    }
  
    header h1 {
      color: black;
    }
  }
  
  @media screen and (max-width: 576px) {
    h2 {
      font-size: 275%;
    }
  
    header {
      font-size: 150%;
      padding-top: 10px;
      padding-bottom: 300px;
    }
  
    header h1 {
      width: 100%;
      margin-left: auto;
      margin-right: auto;
    }
  
    header ul li {
      width: 100%;
    }
  
    .about-me p {
      margin-left: 10px;
      margin-right: 10px;
    }
  
    .custom-container,
    .custom-container a,
    .custom-container img {
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      height: 375px;
    }
  
    .contact-me ul {
      font-size: 150%;
      padding-left: 0;
    }
  
    .contact-me ul li {
      margin-left: 30px;
      margin-right: 30px;
    }
  }
  
  @media screen and (max-width: 370px) {
    header {
      padding-bottom: 420px;
    }
  }
